/* Global*/
*{
    position: relative;
    box-sizing: border-box;
}
/* Custom */
.elements{
    background-color: rgba(0,0,0,0.5);
    border: 1px solid #fff;
    border-radius: 10px;
    color: #fff;
    font-size: 20px;
    padding: 10px;

}
/* pointer */
.u-pointer{
    cursor: pointer;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

/* padding */
.givePadding{
    padding-top: 15.5vh;
    padding-bottom: 11vh;
}
.titlePadding{
    padding-top: 3vh;
    padding-bottom: 3vh;
}
.wrapper{
	overflow: hidden;
}
.u-clearfix:before,
.u-clearfic:after,
.container:before,
.container:after,
.container-fluid:before,
.container-fluid:after,
.row:before,
.row:after {
    content: " ";
    display: table;
}
.u-clearfix:after,
.container:after,
.container-fluid:after,
.row:after {
    clear: both;
}
